nav.navbar, .navbar-default {
  /* background-color: #ffffff !important; */
  background-color: white;

}
.navbar-nav{
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    margin-left: 25%;
}

.container{
  display: block;
  margin: auto;
}

.company-name{
  display: table-cell;
  vertical-align: middle;
}

.navbar-header {
    width: 20%
}

.navbar-brand>img {
  width: 75%;
}

.pages {
  display: block;
  margin: auto;
}

.logo {
  display: block;
  max-width: 45px;
}

.pages.navbar{
  height: 6vh;
  width: 100vw;
}

.navitem{
    margin: 0px 15px;
}

.navbar-default {
  background-image: none;
  border: none;
  border-radius: 0;
}

.navbar-default .navbar-nav > li > a {
  color: #232323 !important ;
}

.navbar-brand {
  color: #025253 !important;
}

.navbar-brand{
    display: table;
}


/* mobile */
@media only screen and (max-width: 600px) {
  .container {
    padding-left: 0;
    padding-right: 0;
  }

  .pages.navbar{
    height: inherit;
    margin: auto;
    font-size: 1.2rem;
    padding: 12px 7px 2px 27px;
  }
  
  .navbar-nav{
    margin-left: 0;
  }

  .navitem{
    margin: 0px 5px;
  }

  .logo {
    width: 10vw;
  }
}