.carousel-container{
    max-width: 75vw;
    max-height: 75%;
    height: auto;
    margin: auto;
    background-color: inherit !important;
    margin-bottom: 1vh;
}

.legend {
    background: none !important;
    font-size: 150% !important;
}

.btn.btn-primary.mt-auto {
    color: white;
}

.img-fluid {
  position:relative;
    left: 6%;
    object-fit: cover;
    max-width:100%;
    max-height:100%;
}

.col-md-4 {
  position:relative;
  left: 0
}

.my-4 {
  position:relative;
  left: 4%;
  color: #025253;
}

b{
    color: #e2e2e2;
}

p{
    color: #bcbcbc;
}

.my-3{
    color: #e2e2e2;
}

ul{
    color: #bcbcbc;
}

.twitter-timeline{
   float: right;
}

.news-card{
    float: left;
    margin-left: 2%;
    margin-right: 2%;
    width: 20%;
}

.card-img-top {
  margin-top: 20px;
  width: 100%!important;
}

#developer-img {
    height: auto!important;
    margin-top: 0;
}

.card-body{
  object-fit: contain;
}

body{
  background-color: #ffffff !important;
}

.jumbotron{
  background-color: #1c1c1c;
  color: #bcbcbc;
}

.jumbotron-heading{
    color: #025253 !important;
}

.list-group-item{
    background-color: #232323;
    color: #e2e2e2;
}

.card{
  background-color: #232323;
  width: 100%;
  border-radius: 0;
}

.card:hover{
  z-index: 2000;
  transition: height 0.3s, box-shadow 0.3s;  
  box-shadow: 20px 20px 40px 0px rgba(0,0,0,0.5);
  transform: scale(1.05);
}

.card-title{
    display: block;
    margin-left: auto;
    margin-right: auto;
    color: #e2e2e2;
}

.card-title.about-card-title{
    color: #025253;
}

a {
    color: #bcbcbc;
}

.card-text{
    color: #bcbcbc;
}

.related-instances > div > div{
  width: 70%;
  margin-left: 12%;
  margin-bottom: 5%;
}

.navbar{
  background-color: #201F1D;
}

.navbar.sticky-top.navbar-expand-lg.navbar-light{
  color: #DE8D47;
}

nav .navbar-nav li a{
  color: white !important;
}

.control-arrow{
  opacity: 1 !important;
  position: absolute !important;
  margin: auto;
  color: black !important;
}

@media only screen and (max-width: 600px) {
  html, body, #root{
    font-size: .7rem;
  }
  

  .carousel-container {
    height: inherit;
  }

  .carousel.carousel-slider, .image, .slide, .slide-carousel{
    max-width: 95vw !important;
    max-height: 95vh !important;
  }

  .carousel, .slider-wrapper{
    max-height: 95vh;
    max-width: 95vw;
  }

  .carousel{
    margin-bottom: 5vh !important;
  }

}