}

@media only screen and (max-width: 600px) {
  html, body, #root{
    font-size: .7rem;
  }
  

  .carousel-container {
    height: inherit;
  }

  .carousel.carousel-slider, .image, .slide, .slide-carousel{
    max-width: 95vw !important;
    max-height: 95vh !important;
  }

  .carousel, .slider-wrapper{
    max-height: 95vh;
    max-width: 95vw;
  }

  .carousel{
    margin-bottom: 5vh !important;
  }

}
body {
  margin: 0;
