.jumbotron{
    margin-bottom: 0px; 
    max-height: 37vh;
    border-radius: 0;
    z-index: 1100;
    box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.2),0px 4px 5px 0px rgba(0,0,0,0.14),0px 1px 10px 0px rgba(0,0,0,0.12);
}

.bottom-container>p>a, .bottom-container>p {
    color: #e2e2e2;
}

.jumbotron.bottom {
    margin-top: 1vh;
    max-height: 15vh;
    min-height: 10vh;
    position: fixed;
    bottom: 0;
    width: 100%;
    border-radius:0;
    padding-top: .8em;
    padding-bottom: .8em;
}

.logo-bottom{
display: block;
  max-width:100px;
  max-height:65px;
  width: auto;
  height: 100%;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(-50%, -50%);

}

.logo-col{
    min-height: 60px;
}

.direccion {
    text-align: left;
    margin-left: -15px;
}

.consultas {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

@media only screen and (max-width: 600px) {

    .jumbotron{
        padding: 16px 8px 16px 8px;
    }

    .lead{
        font-size: .7rem;
    }

    .jumbotron-heading{
        font-size: 1.5rem;
    }

    .jumbotron.bottom{
        margin-top: 1%;
        
        height: 29vh;
        font-size: 1rem;
    }

    .logo-bottom{
        width: auto;
        padding-bottom: 2%;
    }
}